import React, { useState, useRef } from "react";
import firebase, { db, storage } from "../../../config/Firebase";
import "./panel.css";
import Icon from "../../../utils/Icon";
import { Link } from "react-router-dom";
import Editor from "jodit-react";
import Header from "../../../components/Header";
import Sidebar from "../../../components/Sidebar";
import Input from "../../../utils/Input";
import Footer from "../../../components/Footer";
const Panel = ({ history }) => {
  const [title, setTitle] = useState("");
  const [link, setLink] = useState("");
  const [color, setColor] = useState("");
  const [list, setList] = useState([]);
  const [imgs, setImages] = useState([]);
  const form = useRef(null);
  const handleClick = () => {
    firebase
      .auth()
      .signOut()
      .then(() => history.push("/"))
      .catch((error) => {});
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const joditEditor = document.querySelector(".project_form .jodit-wysiwyg");
    if (imgs.length > 0) {
      if (joditEditor.textContent) {
        form.current.classList.add("show_on_success");
        const details = joditEditor.innerHTML;
        Promise.all(
          imgs.map((img, i) =>
            storage.ref(`/projects/${title}/${title}_${i}`).put(img)
          )
        )
          .then(() => {
            Promise.all(
              imgs.map((img, i) =>
                storage.ref(`/projects/${title}/${title}_${i}`).getDownloadURL()
              )
            )
              .then((images) => {
                db.collection("projects")
                  .add({ title, link, details, images, color, list })
                  .then(() => {
                    form.current.classList.remove("show_on_success");
                    setTitle("");
                    setLink("");
                    document.querySelector(
                      ".project_form .jodit-wysiwyg"
                    ).innerHTML = "";
                    setList([]);
                    setColor("");
                    setImages([]);
                  })
                  .catch((error) => {});
              })
              .catch((error) => {});
          })
          .catch((error) => {});
      } else {
      }
    } else {
    }
  };
  const removeButtons = [
    "|",
    "outdent",
    "indent",
    "|",
    "font",
    "fontsize",
    "brush",
    "image",
    "file",
    "video",
    "link",
    "table",
    "|",
    "align",
    "selectall",
    "cut",
    "copy",
    "paste",
    "copyformat",
    "symbol",
    "print",
    "preview",
    "find",
    "about",
  ];
  const buttons = ["source", "bold", "italic", "paragraph", "|", "ul", "ol"];
  return (
    <div className="add_panel">
      <Header admin={true} />
      <Sidebar admin={true} />
      <div className="outer_container">
        <form
          onSubmit={handleSubmit}
          id="accesspanel"
          className="container adding_form project_form"
          method="post"
        >
          <div className="loader_success" ref={form}>
            <Icon loader={true} />
          </div>
          <h1 id="litheader" className="light_heading">
            ADD NEW PROJECT
          </h1>
          <div className="row">
            <Input
              onChange={(e) => setTitle(e.target.value)}
              value={title}
              type="text"
              id="title"
              placeholder="Title"
            />
            <Input
              onChange={(e) => setLink(e.target.value)}
              value={link}
              type="url"
              id="link"
              placeholder="Link of Project"
            />
          </div>
          <div className="row">
            <div className="col-12">
              <label htmlFor="details">details:</label>
              <div className="btn_animate jodit_editor input_animate">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <div>
                  <Editor
                    config={{
                      theme: "black",
                      removeButtons,
                      buttonsSM: buttons,
                      buttonsXS: buttons,
                    }}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            <label className="col-12">Upload Images</label>
            <div className="col-lg-6  col-12 ">
              <label htmlFor="files">
                <div className="btn_animate">
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  Upload Pictures &nbsp;
                  <span className="shift">›</span>
                  <div className="mask"></div>
                </div>
              </label>
              <input
                type="file"
                onChange={(e) => {
                  if (e.target.files) {
                    setImages(Array.from(e.target.files));
                  }
                }}
                multiple
                id="files"
                style={{ display: `none` }}
              />
            </div>
            <div className="col-lg-6  col-12 ">
              <div className="btn_animate input_animate">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <input
                  readOnly={true}
                  type="text"
                  value={imgs.map((el, i) =>
                    i < imgs.length - 1 ? el.name + " " : el.name + "."
                  )}
                  className="browser-default"
                  placeholder="No File Chosen"
                />
              </div>
            </div>
          </div>
          <div className="row">
            <Input
              onChange={(e) => setColor(e.target.value)}
              value={color}
              type="text"
              id="color"
              placeholder="Color"
            />
            <Input
              onChange={(e) => setList(e.target.value.split(","))}
              value={list.join(",")}
              type="text"
              id="Languages"
              placeholder="Languages"
            />
          </div>
          <div className="center btn_wrapper">
            <button className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Add new project
              <span className="shift">›</span>
            </button>
            <Link to="/" className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Home
            </Link>
            <div
              className="btn_animate"
              style={{ display: `inline-block` }}
              onClick={handleClick}
            >
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Logout &nbsp;
              <span className="shift">›</span>
            </div>
          </div>
        </form>
        <Footer />
      </div>
    </div>
  );
};

export default Panel;
